<template>
    <el-form  label-width="80px" label-position="left">
        <el-row>
            <el-col :span="24">
                <el-row type="flex" justify="center">
                    <el-col :span="4">
                        <el-form-item label="">
                            <el-date-picker v-model="periodCode" value-format="yyyyMM" format="yyyyMM" type="month" placeholder=""></el-date-picker>
                        </el-form-item>
                    </el-col>
                    <el-col :span="5" :offset="1" >
                        <el-button type="primary" @click="onSearch"> </el-button>
                    </el-col>
                </el-row>
            </el-col>
        </el-row>
        
       <el-row>
            <el-col :span="24">
                <el-table 
                    :data="tableData" 
                    size="mini" 
                    v-loading="loadingTable" 
                    element-loading-text=""
                    element-loading-spinner="el-icon-loading">
                    <el-table-column prop="periodCode" label="" align="center" width="160px">
                    </el-table-column>
                    <el-table-column prop="valBefoerDesc" label="" align="center" width="160px"> 
                    </el-table-column>
                    <el-table-column prop="valBefoer" label="" align="center" width="160px">
                    </el-table-column>
                    <el-table-column prop="valAfterDesc" label="" align="center" width="160px">
                    </el-table-column>
                    <el-table-column prop="valAfter" label="" align="center" width="160px">
                    </el-table-column>
                    <el-table-column prop="updateTime" label="" sortable align="center" width="160px">
                    </el-table-column>
                    <el-table-column prop="updateBy" label="" align="center" width="160px">
                    </el-table-column>
                    <el-table-column prop="updateMemo" label="" align="center">
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
        <br>
        <el-row type="flex" justify="center">
            <el-col :span="8" align="center">
                <el-pagination
                    :page-size="pageData.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :page-sizes="[10, 20, 30, 50,pageData.total]"
                    :total="pageData.total"
                    :current-page="pageData.currentPage"
                    @current-change="onChangePage"  
                    @size-change="handleSizeChange">
                </el-pagination>
            </el-col>
        </el-row>
    </el-form>
</template>


<script>
export default {
    data() {
        return {
            loadingTable:false, //
            form: {
                id: "", //
                name: "", //
            },
            //
            tableData: [],
            //
            pageData:{
                currentPage:1,
                pageSize:10,
                total:null,
            },
            //
            periodCode:""
        };
    },
    methods: {
        //
        onSearch() {
            this.loadingTable = true;  
            this.$axios({
                method:'post',
                url:"/apis/member/findPeriodLogAll",
                params:{
                    periodCode:this.periodCode,
                    currentPage:this.pageData.currentPage,
                    pageSize:this.pageData.pageSize,
                    date:new Date().getTime()
                }
            })     
            .then(response=>{
                console.log(response.data)
                if(response.data.code){
                    this.pageData.currentPage = response.data.data.pageNum;
                    this.pageData.pageSize = response.data.data.pageSize;
                    this.pageData.total = response.data.data.total;
                    let list = response.data.data.list;
                    // for(let i= 0; i < list.length; i++){
                    //     //
                    //     if(list[i].calStatus==0){
                    //         list[i].calStatus = "";
                    //     }else if(list[i].calStatus==1){
                    //         list[i].calStatus = "";
                    //     }else if(list[i].calStatus==2){
                    //         list[i].calStatus = "";
                    //     }else if(list[i].calStatus==3){
                    //         list[i].calStatus = "";
                    //     }
                    //     //
                    //     if(list[i].salesStatus==0){
                    //         list[i].salesStatus = "";
                    //     }else if(list[i].salesStatus==1){
                    //         list[i].salesStatus = "";
                    //     }else if(list[i].salesStatus==2){
                    //         list[i].salesStatus = "";
                    //     }else if(list[i].salesStatus==3){
                    //         list[i].salesStatus = "";
                    //     }
                    //     //
                    //     if(list[i].bonusStatus==0){
                    //         list[i].bonusStatus = "";
                    //     }else if(list[i].bonusStatus==1){
                    //         list[i].bonusStatus = "";
                    //     }
                    // }
                    this.pageData.currentPage = response.data.data.pageNum;
                    this.pageData.pageSize = response.data.data.pageSize;
                    this.tableData = response.data.data.list;
                }
                setTimeout(()=>{
                    this.loadingTable = false;
                },200)
            })
        },
        //
        onChangePage(currentPage) {
            this.form.currentPage = currentPage;
            this.onSearch();
        },
        //
        handleSizeChange(pageSize) {
            this.pageData.pageSize = pageSize;
            this.onSearch();
        }
    },
    created() {
        this.onSearch();
    }
};
</script>




