<template>
    <el-form  label-width="80px" label-position="left">
        <el-row>
            <el-col :span="24">
                <el-row type="flex" justify="center">
                    <el-col :span="4">
                        <el-form-item label="">
                            <el-date-picker v-model="periodCode" value-format="yyyyMM" format="yyyyMM" type="month" placeholder=""></el-date-picker>
                        </el-form-item>
                    </el-col>
                    <el-col :span="5" :offset="1" >
                        <el-button type="primary" @click="onSearch"> </el-button>
                        <el-button type="primary" @click="onSearch"> </el-button>
                    </el-col>
                </el-row>
            </el-col>
        </el-row>
        <br>
        <el-row>
            <el-col :span="24">
                <el-table 
                    :data="tableData" 
                    border
                    size="mini" 
                    v-loading="loadingTable" 
                    element-loading-text=""
                    element-loading-spinner="el-icon-loading">
                    <el-table-column prop="" label="" align="center" width="100px">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center" width="100px"> 
                    </el-table-column>
                    <el-table-column prop="" label="" align="center" width="100px">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center" width="100px">
                    </el-table-column>
                    <el-table-column prop="" label="VIP" align="center" width="100px">
                    </el-table-column>
                    <el-table-column prop="" label=""  align="center" width="100px">
                    </el-table-column>
                    <el-table-column prop="" label="(PV)" align="center" width="100px">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="(PV)" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="(PV)" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="(PV)" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="" align="center">
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
        <br>
        <el-row type="flex" justify="center">
            <el-col :span="8" align="center">
                <el-pagination
                    :page-size="pageData.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :page-sizes="[10, 20, 30, 50,pageData.total]"
                    :total="pageData.total"
                    :current-page="pageData.currentPage"
                    @current-change="onChangePage"  
                    @size-change="handleSizeChange">
                </el-pagination>
            </el-col>
        </el-row>
    </el-form>
</template>


<script>
export default {
    data() {
        return {
            loadingTable:false, //
            form: {
                id: "", //
                name: "", //
            },
            //
            tableData: [],
            //
            pageData:{
                currentPage:1,
                pageSize:10,
                total:null,
            },
        };
    },
    methods: {
        //
        onChangePage(currentPage) {
            this.form.currentPage = currentPage;
            //this.onSearch();
        },
        //
        handleSizeChange(pageSize) {
            this.pageData.pageSize = pageSize;
            //this.onSearch();
        },
        //
        onShowDetails(data) {
            
        },
        //
        back() {
            this.$router.go(-1);
        }
        
    },
    created() {
        
    }
};
</script>





